(function () {
  'use strict';

  angular.module('BlurAdmin.theme')
    .service('themeLayoutSettings', themeLayoutSettings);

  /** @ngInject */
  function themeLayoutSettings(baConfig) {
    var isMobile = (/android|webos|iphone|ipad|ipod|blackberry|windows phone/).test(navigator.userAgent.toLowerCase());
    var mobileClass = isMobile ? 'mobile' : '';
    var blurClass = baConfig.theme.blur ? 'blur-theme' : '';
    angular.element(document.body).addClass(mobileClass).addClass(blurClass);

    return {
      blur: baConfig.theme.blur,
      mobile: isMobile,
    }
  }

})();
(function () {
	'use strict';

	angular.module('BlurAdmin.api')
	.service('civilServiceEligibilityService', civilServiceEligibilityService);

	/** @ngInject */
	function civilServiceEligibilityService($q) {

		this.getByEmployeeId = function(employeeId) {
			var defer = $q.defer();
			var CivilServiceEligibilityObject = Parse.Object.extend("CivilServiceEligibility");
			var query = new Parse.Query(CivilServiceEligibilityObject);

			if(employeeId){
				query.equalTo("employeeId", employeeId);
			}

			query.find({
				success: function(results) {
					defer.resolve(results);
				},
				error: function(error) {
					defer.reject(error);
				}
			});
			return defer.promise;
		};
	}
})();

